<template>
  <div class="square">
    <div class="state">
      <div class="state_bt" @click="stateClick">
        <span class="state_bt_span">{{stateText}}</span>
        <img class="state_bt_img" src="../../assets/images/arrow_down.png" alt />
      </div>
      <div class="state_content" v-show="stateShow">
        <span class="state_content_span" @click="stateIndexClick(0)">最新</span>
        <span class="state_content_span" @click="stateIndexClick(1)">问答</span>
        <!-- <span class="state_content_span" @click="stateIndexClick(2)">投票</span> -->
      </div>
    </div>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div class="list" v-for="(item,index) in list" :key="index">
        <div v-if="item.ad == 1">
          <div v-if="item.ad == 1" class="ad-box">
            <img :src="item.pic" />
            <span class>广告</span>
          </div>
        </div>
        <div v-if="item.ad !=1">
          <div class="header" @click="answersDetailsClick(item.tid,item.special)">
            <div class="avatar-box" @click="handleToMyPage(item)">
              <img :src="item.avatar" class="portrait_img" alt />
              <img
                class="isvip"
                src="../../assets/images/icon_isvip.png"
                v-if="item.is_ident_icon == 1"
              />
            </div>
            <div class="header_center">
              <span class="header_center_name">{{item.author}}</span>
              <div class="header_center_label">
                <div class="label_grade">
                  <img
                    class="label_grade_img"
                    v-if="item.is_carvip == 1"
                    src="../../assets/images/icon_iscarvip.png"
                    alt
                  />
                  <span class="label_grade_name">
                    <!-- <i v-show="item.is_carvip == 1">{{item.show_car}}</i> -->
                    <i>{{item.level}}</i>
                  </span>
                </div>
                <div class="label_heat">
                  <img class="label_heat_img" src="../../assets/images/icon_label.png" alt />
                  <span class="label_grade_name">{{item.extcredits2}}°</span>
                </div>
              </div>
            </div>
            <div class="header_right">
              <span class="header_right_monry" v-show="item.question_price > 0">¥ {{item.question_price}}</span>
              <span class="header_right_time">{{item.time_string}}</span>
            </div>
            <img
              v-show="item.is_reward_icon == 1"
              class="header_right_img"
              src="../../assets/images/icon_reward.png"
              alt
            />
            <img
              v-show="item.is_reward_icon == 2"
              class="header_right_img"
              src="../../assets/images/icon_equalization.png"
              alt
            />
            <!-- <img
              v-show="item.is_reward_icon == 3"
              class="header_right_img"
              src="../../assets/images/icon_voted.png"
              alt
            /> -->
          </div>
          <div class="answers_content" @click="answersDetailsClick(item.tid,item.special)">
            <span class="answers_content_text">{{item.message}}</span>
          </div>
          <div class="answers_img" v-show="item.image_list != []">
            <img
              v-for="(itemt,index) in item.image_list"
              @click="answersImageClick(item.image_list,index)"
              :key="index"
              :src="itemt"
              class="answers_image"
              alt
            />
          </div>
          <!--投票-->
          <!-- <div class="option" v-if="item.special == 1">
            <div class="option_up">
              <span class="option_up_left">投票选项</span>
              <div
                class="option_up_right"
                v-show="item.vote.poll.is_expiration&&item.vote.poll.is_expiration == 0"
              >
                <span class="option_up_right_text">据截止还有</span>
                <span class="option_up_right_time">
                  <van-count-down
                    format="HH:mm:ss"
                    millisecond
                    :time="item.vote.poll.expiration/1000"
                  />
                </span>
              </div>
              <span v-show="item.vote.poll.is_expiration == 1" class="option_up_span">投票已结束</span>
            </div>
            <div class="option_down" v-for="(items,index) in item.vote.polloption" :key="index">
              <div class="option_center">
                <img :src="items.icon" class="option_down_img" alt />
                <span class="option_down_center">{{items.polloption}}</span>
              </div>
              <div class="option_center_content">
                <img
                  v-show="item.vote.poll.is_vote == items.polloptionid"
                  class="option_center_image"
                  src="../../assets/images/icon_voted.png"
                  alt
                />
                <span
                  class="option_down_text"
                  @click="voteOwnClick(item.tid,items.polloptionid)"
                  v-show="item.vote.poll.is_expiration == 0"
                >投票</span>
                <span
                  class="option_down_span"
                  v-show="item.vote.poll.is_expiration == 1"
                >{{items.votes}}票</span>
              </div>
            </div>
          </div> -->
          <div class="answers_news" v-show="item.show_name != ''">
            <div class="answers_news_up">
              <span class="answers_news_up_text">{{item.show_name}} 回答：</span>
            </div>
            <span class="answers_news_dow">{{item.show_message}}</span>
          </div>
          <div class="nction">
            <div class="nction_see">
              <img class="nction_see_img" src="../../assets/images/see.png" alt />
              <span class="nction_see_text">{{item.views}}</span>
            </div>
            <div class="nction_see" @click="answersDetailsClick(item.tid,item.special)">
              <img class="nction_see_img" src="../../assets/images/news.png" alt />
              <span class="nction_see_text">{{item.replies}}</span>
            </div>
            <div class="nction_see" @click="handleShare(item)">
              <img class="nction_see_img" src="../../assets/images/share.png" alt />
              <!-- <span class="nction_see_text">26</span> -->
            </div>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>
<script>
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      allpage: -1,
      loading: false,
      finished: false,
      item: {
        token: this.$store.state.userData,
        type: 1,
        page_size: 10,
        page_index: 0
      },
      list: [],
      ImagePreviewDialog: "",
      stateShow:false,
      stateText:'最新'
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.answersItem();
    },
    // 跳转我的页面
    handleToMyPage(item){
      if(!this.$store.state.userData){
        //  window.location.href= "teldapp://login"
          let frame = document.createElement("frame");
              frame.src ="teldapp://login";
          document.documentElement.childNodes[0].appendChild(frame);        
          return null;
      }
      if(item.is_self == 1){
        this.$router.push({path:'/my/index/square'})
      }else {
        this.$router.push({path:'/my/index/square',query:{uid:item.authorid}})
      }
      //this.$router.push({path:'/my/index/square',query:{uid:''}})
    },
    stateIndexClick(index) {
      if(index == 1) {
        this.stateText = '问答'
      }else if(index == 2){
        this.stateText = '投票'
      }else {
        this.stateText = '最新'
      }
      this.item.type = index;
      this.list = []
      this.answersItem();
      this.stateShow = false
    },
    //最新赛选
    stateClick() {
      this.stateShow = !this.stateShow
    },
    //查看图片
    answersImageClick(image, index) {
      this.ImagePreviewDialog = ImagePreview({
        images: image,
        startPosition: index,
        loop:false
      });
    },
    //投票
    voteOwnClick(tid, optionid) {
      this.api
        .addVote({
          token: this.$store.state.userData,
          tid: tid,
          optionid: optionid
        })
        .then(res => {
          if (res.err_code == 0 && res.data == "") {
            this.$toast("投票成功");
            this.init();
          } else if (res.data.status == -1) {
            this.$toast(res.data.msg);
          }
        });
    },
    answersItem() {
      this.api.answersItem(this.item).then(res => {
        if (res.err_code == 0) {
          this.allpage = res.data.total_page;
          this.list = [...this.list, ...res.data.forum_thread_data];
        }
        this.loading = false;
      });
    },
    handleShare(item){
      if(item.special == 0){
        let img = item.image_list.length ? item.image_list[0] :  item.avatar;
        let url = `${this.$store.state.indexUrl+'answers/answersDetails?share=1&id='+item.tid+'&'+this.$store.state.appParams}`,
            json = {url:url,channel:'wxp,wxf,sina,qqp,qqz',title:`【E区】我在E区发布了一条有偿问答，快来帮帮我吧`,desc:item.message,image:img};
        console.log(url);
        window.location.href= `teldapp://share?params=`+JSON.stringify(json)
      }else {

      }
     

    },
    onLoad() {
      if (this.list.length) {
        this.item.page_index++;
        this.answersItem();
      }
      if (this.item.page_index == this.allpage) {
        this.finished = true;
      }
    },
    //回答详情
    answersDetailsClick(id, special) {
      console.log(id,special);
      if (special == 0) {
        // let url = `${this.$store.state.indexUrl +
        //     "answers/answersDetails?id=" +
        //     id +
        //     "&" +
        //     this.$store.state.appParams}`,
        //   json = { url: url, share: "0" ,keyboard:'1'};
        // if (!this.$store.state.userData) {
        //  // window.location.href = "teldapp://login";
        //  let frame = document.createElement("frame");
        //     frame.src ="teldapp://login";
        //   document.documentElement.childNodes[0].appendChild(frame);
        // } else {
          this.$router.push({
            path: "/answers/answersDetails",
            query: { id: id }
          });
        //   window.location.href =
        //     `teldapp://newWebview?params=` + JSON.stringify(json);
        // }
      } else {
        
        // let url = `${this.$store.state.indexUrl +
        //     "answers/voteDetails?id=" +
        //     id +
        //     "&" +
        //     this.$store.state.appParams}`,
        //   json = { url: url, share: "0",keyboard:'1' };
        // if (!this.$store.state.userData) {
        //  // window.location.href = "teldapp://login";
        //  let frame = document.createElement("frame");
        //     frame.src ="teldapp://login";
        //   document.documentElement.childNodes[0].appendChild(frame);
        // } else {
          this.$router.push({
            path: "/answers/voteDetails",
            query: { id: id }
          });
        //   window.location.href =
        //     `teldapp://newWebview?params=` + JSON.stringify(json);
        // }
      }
    }
  }
};
</script>
<style lang="less" scoped rel="stylesheet/less">
.square {
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
  .list {
    padding: 15px 15px;
  }
}
.state {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 11px;
  position: relative;
  border-radius:15px;
  .state_bt {
    display: flex;
    flex-direction: row;
    align-items: center;
    .state_bt_span {
      color: #070c29;
      font-size: 16.5px;
      margin-left: 15px;
    }
    .state_bt_img {
      width: 20px;
      height: 20px;
      margin-left: 5px;
    }
  }
  .state_content {
    position: absolute;
    top: 25px;
    left: 32px;
    z-index: 1;
    width: 70px;
    background: #ffffff;
    box-shadow: 0px 7px 5px 0px rgba(7, 12, 41, 0.05);
    display: flex;
    flex-direction: column;
    align-items: center;
    .state_content_span {
      color: #c7c7c7;
      font-size: 15px;
      height: 40px;
      display: flex;
      align-items: center;
    }
  }
}
.ad-box {
  position: relative;
  img {
    width: 100%;
    border-radius: 8px;
  }
  span {
    position: absolute;
    top: 0px;
    right: 0px;
    display: inline-block;
    color: #ffffff;
    background-color: #0bc7f0;
    padding: 0 3px;
    border-radius: 0 8px 0 8px;
    font-size: 13px;
  }
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* flex-wrap: nowrap; */
  width: 100%;
  align-items: center;
  position: relative;
  height: 50px;
}
.header_center {
  display: flex;
  flex-direction: column;
  width: 200px;
}
.avatar-box {
  position: relative;
  margin-right: 13px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.portrait_img {
  width: 42px;
  height: 42px;
  border-radius: 100% 100%;
  object-fit: cover;
  flex: none;
  border: 1px solid;
}
.isvip {
  position: absolute;
  width: 14px;
  height: 14px;
  bottom: 6px;
  right: 1px;
}
.header_center_label {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-top: 4.5px;
}
.header_center_name {
  font-size: 16px;
  color: #4e5578;
}
.label_grade {
  // width: 92.5px;
  height: 15px;
  border: 1px solid #0ec6f1;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
}
.label_grade_img {
  width: 10px;
  height: 9px;
}
.label_grade_name {
  font-size: 10px;
  color: #0ec6f1;
  margin-left: 2px;
}
.label_heat {
  height: 15px;
  border: 1px solid #0ec6f1;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  padding: 0 5px;
}
.label_heat_img {
  width: 8px;
  height: 13px;
}
.header_right {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  height: 50px;
  .header_right_monry {
    color: #ef5277;
    font-size: 16.94px;
  }
  .header_right_time {
    font-size: 11.5px;
    color: #888888;
  }
}
.header_right_img {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 6px;
  right: 0;
}
.answers_content {
  padding: 9px 0;
  .answers_content_text {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    font-size: 15px;
    word-break: break-all;
    color: #000000;
  }
}
.answers_img {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .answers_image {
    width: 110px;
    height: 110px;
    object-fit: cover;
  }
  .answers_image:nth-of-type(2) {
    margin-left: 4px;
  }
  .answers_image:nth-of-type(3) {
    margin-left: 5px;
  }
  .answers_image:nth-of-type(4) {
    margin-top: 4px;
  }
  .answers_image:nth-of-type(5) {
    margin-left: 4px;
    margin-top: 5px;
  }
  .answers_image:nth-of-type(6) {
    margin-left: 4px;
    margin-top: 5px;
  }
  .answers_image:nth-of-type(7) {
    margin-top: 4px;
  }
  .answers_image:nth-of-type(8) {
    margin-left: 4px;
    margin-top: 5px;
  }
  .answers_image:nth-of-type(9) {
    margin-left: 4px;
    margin-top: 5px;
  }
}
.answers_news {
  background-color: #f0f0f0;
  padding: 15px 0;
  margin: 13px 0;
  position: relative;
  width: 100%;
  .answers_news_up {
    display: flex;
    flex-direction: row;
    .answers_news_up_text {
      font-size: 15px;
      color: #424e77;
      padding-left: 19px;
    }
  }
  .answers_news_dow {
    word-wrap: break-word;
    z-index: 555;
    font-size: 13.5px;
    color: #111111;
    padding: 13px 0 0 19px;
    width: 310px;
    display: flex;
  }
  .answers_news_up_img {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0;
    right: 0;
  }
}
.nction {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin-top: 13px;
  .nction_see {
    display: flex;
    align-items: center;
    .nction_see_img {
      width: 16px;
      height: 16px;
    }
    .nction_see_text {
      font-size: 11.5px;
      color: #424e77;
      margin-left: 9.5px;
    }
  }
  .nction_see:nth-of-type(2) {
    margin-left: 19.5px;
  }
  .nction_see:nth-of-type(3) {
    margin-left: 19.5px;
  }
}
.option {
  display: flex;
  flex-direction: column;
  padding: 24px 0 12.5px 0;
  .option_up {
    display: flex;
    justify-content: space-between;
    .option_up_left {
      font-size: 15px;
      color: #000000;
    }
    .option_up_right {
      display: flex;
      flex-direction: row;
      .option_up_right_text {
        width: 104px;
        height: 19.5px;
        background-color: #888888;
        border-radius: 10px 0px 0px 10px;
        font-size: 15px;
        color: #fefeff;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .option_up_right_time {
        width: 75px;
        height: 17px;
        border-radius: 0px 10px 10px 0px;
        border: 1.5px solid #888888;
        font-size: 10px;
        color: #000000;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
    .option_up_span {
      width: 100px;
      height: 20px;
      background-color: #888888;
      border-radius: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      color: #fefeff;
    }
  }
  .option_down {
    height: 60px;
    background-color: #f0f0f0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 9px;
    .option_center {
      height: 60px;
      display: flex;
      flex-direction: row;
      align-items: center;
      .option_down_img {
        width: 70px;
        height: 45px;
        margin: 7.5px 20px 7.5px 20px;
      }
      .option_down_center {
        font-size: 14.5px;
        color: #111111;
      }
    }
    .option_center_content {
      display: flex;
      flex-direction: row;
      align-items: center;
    }
    .option_center_image {
      width: 55px;
      height: 55px;
      margin-right: 15px;
    }
    .option_down_text {
      width: 45.5px;
      height: 19.5px;
      border: 1.5px solid #626262;
      border-radius: 6px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 13.5px;
      color: #111111;
      margin-right: 17.5px;
    }
    .option_down_span {
      font-size: 13.5px;
      color: #888888;
      margin-right: 22.5px;
    }
  }
}
</style>